import React, { useState, useEffect, useCallback } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import style from "./style.css";
import {
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography,
} from "@mui/material";
import { Form, Container, Row, Col, Modal } from "react-bootstrap";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";
import Dialog from "@mui/material/Dialog";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

function UpdateUser(props) {
  let [items, setItems] = useState([]);
  let [show, setShow] = useState(false);

  let [row, setRow] = useState([]);
  let [companyName, setCompanyName] = useState("");
  let [companyCategory, setCompanyCategory] = useState("");
  let [errors, setErrors] = useState({});
  let [open, setOpen] = useState(false);
  let [alart, setAlart] = useState("");
  let [severity, setSeverity] = useState("");
  let [openDialog, setOpenDialog] = useState(false);

  function modalClose() {
    setShow(false);
  }

  const findFormErrors = () => {
    const newErrors = {};
    // name errors
    if (!companyName || companyName === "")
      newErrors.companyName = "cannot be blank!";
    return newErrors;
  };

  const style = {
    marginTop: "350px", // Add a valid margin-top value here
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 1000,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };
  <style>
    {`
        .custom-select {
          position: relative;
          display: inline-block;
          padding-right: 20px;
        }

        .custom-select::after {
          content: "";
          position: absolute;
          top: 50%;
          right: 5px;
          transform: translateY(-50%);
          border-width: 0.25em;
          border-style: solid;
          border-color: transparent transparent #000;
          pointer-events: none;
        }
      `}
  </style>;

  function submitCompany(e) {
    e.preventDefault();
    const newErrors = findFormErrors();
    // Conditional logic:
    // console.log(newErrors)
    if (Object.keys(newErrors).length > 0) {
      // We got errors!
      setErrors(newErrors);
    } else {
      axiosInstance
        .post(
          "projonline/addcompany",
          { companyName, companyCategory },
          {
            headers: {
              token:
                "Bearer " +
                JSON.parse(localStorage.getItem("user")).accessToken,
            },
          }
        )
        .then((res) => {
          setSeverity(res.data.variant);
          setAlart("Company added successfully");
          setOpen(true);
          setCompanyName("");
          setShow(false);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }
  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  useEffect(() => {
    setOpenDialog(true);
    axiosInstance
      .get("projonline/users", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setItems(res.data.users);
        setOpenDialog(false);
        // console.log(res.data);
        return;
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const updateTable = () => {
    axiosInstance
      .get("projonline/allcompany", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setRow(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    axiosInstance
      .get("projonline/allcompany", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setRow(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const handleClick = (event, cellValues) => {
    // console.log(cellValues)
    props.history.push({ pathname: "/updateSpecificUser", data: cellValues });
  };

  const handleRowEditeClick = async (event, cellValues) => {
    const companyId = cellValues.row._id;
    const companyName = cellValues.row.companyName;
    const companyCategory = cellValues.row.companyCategory;

    try {
      const response = await axiosInstance.put(
        `projonline/updatecompany/${companyId}`,
        {
          companyName,
          companyCategory,
        },
        {
          headers: {
            token:
              "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
          },
        }
      );

      if (response.status === 200) {
        setAlart("Company update successfully");
        setOpen(true);
        updateTable();
      }
    } catch (error) {
      console.error("Error updating company:", error);
    }
  };

  const addUser = (e) => {
    //console.log(cellValues)
    props.history.push({ pathname: "/addSpecificUser" });
  };

  const columns2 = [
    { field: "companyName", headerName: "Company Name", width: 250 },
    {
      field: "companyCategory",
      headerName: "Company Category",
      width: 250,
      editable: true,
      type: "singleSelect",
      valueOptions: ["Subcon", "Vender"],
      renderCell: (params) => (
        <div className="custom-select">{params.value}</div>
      ),
    },
    {
      field: "To Edit",
      width: 150,
      renderCell: (cellValues) => {
        return (
          <Button
            variant="contained"
            color="primary"
            onClick={(event) => {
              handleRowEditeClick(event, cellValues);
            }}
          >
            Edit
          </Button>
        );
      },
    },
  ];

  const columns = [
    { field: "name", headerName: "Name", width: 180 },
    { field: "username", headerName: "Username", width: 180 },
    { field: "email", headerName: "email", width: 210 },
    {
      field: "company",
      headerName: "company",
      valueGetter: (params) => params.row.company.companyName,
      width: 200,
    },
    { field: "adminRights", headerName: "Admin Rights", width: 150 },
    {
      field: "userCanAssignForTask",
      headerName: "User Can Assign For Tasks",
      width: 150,
    },
    {
      field: "visbilityBasedOn",
      headerName: "VisbilityBasedOn",
      width: 150,
    },
    {
      field: "To Edit",
      width: 150,
      renderCell: (cellValues) => {
        return (
          <Button
            variant="contained"
            color="primary"
            onClick={(event) => {
              handleClick(event, cellValues);
            }}
          >
            To Edit
          </Button>
        );
      },
    },
  ];

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div>
                <div>
                  <Container>
                    <Row>
                      <Col xs={2}>
                        <h1>All Users</h1>
                      </Col>

                      <Col xs={4}></Col>
                      <Col>
                        <div className="vr" />
                      </Col>
                      <Col>
                        <Button
                          variant="contained"
                          color="primary"
                          onClick={(e) => setShow(true)}
                        >
                          Add a new Company
                        </Button>
                      </Col>

                      {/* <Col><div className="vr" /></Col> */}
                      <Col>
                        <Button
                          variant="contained"
                          color="primary"
                          onClick={(e) => addUser(e)}
                        >
                          Add a new user
                        </Button>
                      </Col>
                    </Row>
                  </Container>
                </div>
              </div>
            </div>
            {/* /.container-fluid */}
          </section>
        </div>

        <div style={{ height: 610, width: "100%" }}>
          <DataGrid
            getRowId={(item) => item._id}
            rows={items}
            columns={columns}
            pageSize={15}
            rowsPerPageOptions={[15]}
            rowHeight={30}
            headerHeight={40}
            components={{ Toolbar: GridToolbar }}
          />
        </div>
      </div>

      <Modal
        show={show}
        onHide={modalClose}
        animation={false}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Modal.Header closeButton>
            <Modal.Title>Add a New Company</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form onSubmit={submitCompany}>
              <Row className="mb-3">
                <Form.Group as={Col}>
                  <Form.Label>Company Name</Form.Label>
                  <Form.Control
                    onChange={(e) => setCompanyName(e.target.value)}
                    value={companyName}
                    type="text"
                    placeholder="Enter Company Name"
                    isInvalid={!!errors.companyName}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.companyName}
                  </Form.Control.Feedback>
                </Form.Group>
                <Form.Group as={Col}>
                  <Form.Label>Company Category</Form.Label>
                  <Form.Control
                    as="select"
                    onChange={(e) => setCompanyCategory(e.target.value)}
                    value={companyCategory}
                    isInvalid={!!errors.companyCategory}
                  >
                    <option value="Subcon">Subcon</option>
                    <option value="Vender">Vender</option>
                  </Form.Control>
                  <Form.Control.Feedback type="invalid">
                    {errors.companyCategory}
                  </Form.Control.Feedback>
                </Form.Group>
              </Row>

              <Modal.Footer>
                <Button variant="contained" color="primary" type="submit">
                  Save Company
                </Button>
              </Modal.Footer>
            </Form>
            <div style={{ height: 410 }}>
              <DataGrid
                getRowId={(item) => item._id}
                rows={row}
                columns={columns2}
                pageSize={15}
                rowsPerPageOptions={[15]}
                rowHeight={30}
                headerHeight={40}
                components={{ Toolbar: GridToolbar }}
              />
            </div>
          </Modal.Body>
        </Box>
      </Modal>

      <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={() => setOpen(false)}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert
          onClose={() => setOpen(false)}
          severity={severity}
          sx={{ width: "100%" }}
        >
          {alart}
        </Alert>
      </Snackbar>

      <Dialog
        open={openDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withRouter(UpdateUser);
